section#intro {
	display: none;
	width: 100vw;
	height: 100vh;
	z-index: 1000;
	position: fixed;
	background-color: #6cccd8;
	overflow: hidden;
	img#selahlogo {
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		margin: auto;
	}
	div#scrollMouse {
		cursor: pointer;
		position: absolute !important;
		bottom: 0;
		left: 0;
		right: 0;
		height: 75px;
		width: 75px;
		margin: 0 auto;
		z-index: 2 !important;
	}
	div#introanim {
		overflow: hidden;
		top: 0px;
		bottom: 0px;
		left: 0px;
		right: 0px;
		margin: auto;
		width: 1024px;
		height: 768px;
		position: absolute !important;
		transform-origin: center center !important;
	}
}

section#introback {
	width: 100vw;
	height: 100vh;
}

section#intro,
section#introback {
	display: none;
}


/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#intro {
		div#introanim {
			transform-origin: center right !important;
		}
	}
}
