section#section7 {
	position: relative;
	width: 100vw;
	overflow: hidden;
	background-image: url(../images/global/backgroundgrey.svg);
	height: 450px;

	div.elementscontainer {
		overflow: hidden;
		display: flex;

		> div {
			&.left, &.right {
				width: 50%;
			}

			&.right {
				height: 100%;
				display: flex;
				align-items: center;

				div.text {
					width: 400px;
					margin: 0 auto;

					h1 {
						font-family: isra;
						font-size: 3em;
						font-weight: normal;
						text-align: center;
						margin: 0;
					}

					p {
						line-height: 1.8em;
						font-family: jf;
						text-align: justify;
					}
				}
			}

			&.left {
				display: flex;
				justify-content: center;
				align-items: center;
				height: 100%;

				div.questionImage {
					display: none;
				}

				div.questionSwiper {
					position: relative;
					display: inline-block;
					perspective: 600px;
					transform-style: preserve-3d;
					width: 500px;
					height: 345px;

					div {
						position: absolute;
						margin: auto;
						left: 0;
						right: 0;
						width: 353px;
						height: 244px;
						overflow: hidden;
						bottom: 40px;
						z-index: 3;

						img.question {
							position: absolute;
							width: 100%;
							bottom: 0;
							left: 0;
							transition: 0.5s;
							transform: translateY(110%);

							&.active {
								transform: translateY(-1%);
							}
						}
					}

					img.tablet {
						transform-origin: bottom center;
						position: absolute;
						margin: auto;
						left: 0;
						right: 0;
						z-index: 2;
						width: 100%;
						transition: 1.8s;
						transition-timing-function: ease-in-out;

						&.rotate {
							transform: rotateX(90deg);
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#section7 {
		height: 300px;
		padding-bottom: 30px;

		div.elementscontainer {
			div.right {
				padding: 0 50px;

				div.text {
					h1 {
						font-size: 2.5em;
					}

					p {
						line-height: 1.5em;
					}
				}
			}

			div.left {
				div.questionImage {
					display: inline-block;
					height: 90%;
					text-align: center;

					img {
						height: 90%;
					}
				}

				div.questionSwiper {
					display: none;
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section#section7 {
		background: none;
		padding-bottom: 20px;
		height: auto;

		div.elementscontainer {
			flex-direction: column;

			div.left {
				margin-top: 20px;
				width: 100%;

				div.questionImage {
					height: auto;
					width: 65%;

					img {
						height: auto;
						width: 100%;
					}
				}
			}

			div.right {
				width: 100%;
				padding: 0 20px;

				div.text {
					width: 90%;
					padding-bottom: 20px;
					border-bottom: 1px dashed #58585a;

					p {
						text-align: center;
						font-size: 0.8em;
					}

					h1 {
						margin: 0 0px 10px;
						font-size: 2.6em;
					}
				}
			}
		}
	}
}