section#section6 {
	position: relative;
	width: 100vw;
	height: 410px;
	padding-top: 10px;
	background-color: #ec6625;

	div.inner {
		border-top: 3px dashed white;
		height: 100%;

		div.elementscontainer {
			display: flex;

			div.right,
			div.left {
				width: 50%;
			}

			div.right {
				div.try {
					margin: 0 auto;
					width: 505px;
					position: relative;
					z-index: 2;
					transform: rotateZ(15deg);

					div#tryswiper {
						position: absolute;
						top: 7%;
						left: 5%;
						width: 90%;
						height: 82%;
						z-index: 1;
					}

					img {
						position: relative;
						z-index: 2;
						width: 100%;
					}
				}
			}

			div.left {
				position: relative;
				top: -75px;

				div.logo {
					margin: 0 auto;
					position: relative;
					overflow: hidden;
					height: 170px;
					padding-top: 10px;
					width: 375px;
					display: flex;
					align-items: center;
					justify-content: center;

					img {
						width: 320px;
					}
				}

				div.text {
					position: relative;
					width: 340px;
					margin: 0 auto;

					h1 {
						color: white;
						text-align: center;
						font-size: 3em;
						margin-bottom: 0.2em;
						margin-top: 0;
						font-family: isra;
						font-weight: normal;
					}

					p {
						font-family: jf;
						color: white;
						text-align: justify;
						font-size: 0.8em;
						line-height: 1.8em;
					}
				}

				div.try{
					display: none;
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#section6 {
		height: 275px;

		div.inner {
			div.elementscontainer {
				div.right {
					div.try {
						top: 10px;
						width: 325px;
					}
				}

				div.left {
					div.text {
						p {
							font-size: 0.7em;
						}

						h1 {
							font-size: 2.2em;
							margin-bottom: 0.1em;
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section#section6 {
		background: none;
		height: auto;
		border-top-right-radius: 10px;
		border-top-left-radius: 10px;

		div.inner {
			border-top-right-radius: 10px;
			border-top-left-radius: 10px;
			border: 2px dashed white;

			div.elementscontainer {
				flex-direction: column-reverse;

				div.right {
					display: none;
				}

				div.left {
					border-top-right-radius: 10px;
					border-top-left-radius: 10px;
					padding: 5px;
					background-color: #ec6625;
					top: 0;
					width: 100%;
					margin-bottom: 30%;

					div.inner {
						padding-bottom: 40%;
						div.logo {
							width: 320px;

							img {
								width: 100%;
							}
						}

						div.text {
							width: 80%;

							h1 {
								font-size: 2.4em;
							}

							p {
								text-align: center;
								line-height: 1.5em;
								font-size: 0.7em;
								margin: 10px 0;
							}
						}

						div.try{
							transform: rotateZ(15deg);
							position: absolute;
							display: block;
							width: 80%;
							left: 0;
							right: 0;
							margin: 7% auto 0;
							img.ipad{
								position: relative;
								width: 100%;
								top: 0;
								left: 0;
							}
							img.tryslide{
								position: absolute;
								top: 0;
								right: 0;
								left: 0;
								width: 95%;
								bottom: 0;
								margin: auto;
							}
						}
					}
				}
			}
		}

		> div.inner {
			border: none;
		}
	}
}