$header_transition_duration: 0.5s;

header#header {
	&.loggedin {
		div.header {
			div.elementscontainer {
				div.user {
					display: flex;
				}

				div.left {
					a.login {
						display: none;
					}

					a.register {
						display: none;
					}
				}

				div.right {
					margin-left: auto;
				}
			}
		}
	}

	div.header {
		position: fixed;
		top: -90px;
		width: 100%;
		opacity: 0;
		z-index: 10;
		font-family: jf;
		font-size: 0.7em;
		transition: $header_transition_duration opacity, $header_transition_duration top;
		height: 80px;
		background-color: rgba(255, 255, 255, 0.9);
		box-shadow: 0px 11px 10px -5px rgba(0, 0, 0, 0.3);

		&.show {
			opacity: 1;
			top: 0px;
		}

		&.hide {
			opacity: 0;
			top: -90px;
		}

		&.mobile {
			display: none;
		}

		div.elementscontainer {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: space-between;

			a {
				color: black;
				text-decoration: none;
				margin: 0 5px;

				p {
					margin: 0;
				}
			}

			div.btn {
				display: inline-block;
				border-radius: 30px;
				margin: 0;
				padding: 0px 30px;
				transition: $header_transition_duration background-color;
			}

			div.user {
				display: none;
				padding: 0 10px;
				z-index: 5;
				top: 0;
				right: 0;
				width: 240px;
				height: 80px;
				background-color: #343746;
				justify-content: space-between;
				align-items: center;

				div.image {
					width: 30%;
					height: 100%;
					display: flex;
					justify-content: center;
					align-items: center;

					div.userimage {
						background-size: 105% 105%;
						width: 55px;
						height: 55px;
						border-radius: 50%;
						border: 4px solid white;
						background-image: url("../images/global/user.svg");
					}
				}

				div.info {
					width: 70%;
					height: 100%;
					color: white;
					display: inline-flex;
					flex-direction: column;
					justify-content: center;

					a {
						color: #00aeef;
						cursor: pointer;
					}

					p {
						line-height: 1.2em;
						word-wrap: break-word;
						text-align: center;

						&:first-child {
							font-weight: bold;
						}
					}
				}
			}

			div.right {
				margin-right: 20px;
				height: 100%;
				position: relative;
				display: flex;
				align-items: center;

				div.btn {
					background-color: #cccccc;
					padding: 15px 25px;

					&.active {
						background-color: #ee145b;
						color: white;
					}
				}
			}

			div.left {
				padding: 10px 0;
				left: 0;
				right: 0;
				margin-left: 20px;
				height: 100%;
				background-color: rgba(255, 255, 255, 0);
				bottom: 0px;
				border-radius: 0;

				a {
					display: inline-block;
					height: 100%;

					div.btn {
						display: flex;
						align-items: center;
						height: 100%;
						width: 100%;
						padding: 15px;
						background-color: #00aeef;

						p {
							color: white;
						}
					}

					&.register {
						div.btn {
							border-bottom-left-radius: 0;
							border-top-left-radius: 0;
						}
					}

					&.login {
						cursor: pointer;

						div.btn {
							// border-top-right-radius: 0;
							// border-bottom-right-radius: 0;

						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	header#header {
		div.header {
			height: 60px;

			div.elementscontainer {
				div.right {
					a {
						div.btn {
							padding: 10px 25px;
						}
					}
				}

				div.left {
					display: flex;
					align-items: center;
					padding: 7px 0;

					a {
						div.btn {
							padding: 10px 15px;
						}
					}
				}
			}
		}

		&.loggedin {
			div.header {
				div.elementscontainer {
					div.user {
						height: 60px;
					}

					div.left {
						margin-left: 10px;
					}

					div.right {
						margin-right: 10px;

						a {
							div.btn {
								padding: 10px 15px;
							}
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	header#header {
		div.header {
			div.elementscontainer {
				display: none;
			}

			&.normal {
				display: none;
			}

			&.mobile {
				opacity: 1;
				top: 0;
				display: block;
				height: auto;

				div.elementscontainer {
					display: flex !important;
					justify-content: space-between;

					div.user {
						display: none;
					}

					&.mobile {
						&.show {
							height: 260px;
						}

						height: 0;
						transition: 0.5s height;
						overflow: hidden;
						flex-direction: column;

						a {
							height: 65px;
							margin: 0;
							width: 100%;
							display: inline-block;

							div {
								height: 100%;
								display: flex;
								justify-content: center;
								align-items: center;
								border-bottom: 1px solid #cccccc;

								p {
									font-size: 1.5em;
									text-align: center;
								}
							}

							&:last-child {
								div {
									border-bottom: none;
								}
							}
						}
					}

					div.left {
						margin-left: 10px;

						a {
							div.btn {
								p {
									font-size: 0.9em;
								}
							}
						}
					}

					div.mobileright {
						height: 100%;
						width: 20%;
						display: flex;
						align-items: center;
						padding: 10px;

						img {
							height: 40px;
						}
					}
				}
			}
		}

		&.loggedin {
			div.header {
				&.mobile {
					div.elementscontainer {
						div.left {
							display: none;
						}

						div.user {
							display: flex;
						}
					}
				}
			}
		}
	}
}