section#sectionlast {
	width: 100vw;
	height: 550px;
	padding: 25px 0;
	background-color: #00b4ed;

	> div.inner {
		border-top: 3px dashed white;
		border-bottom: 3px dashed white;
		height: 100%;
		padding: 10px 0;

		div.elementscontainer {
			display: flex;

			div.right {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: space-around;
				width: 40%;
				height: 100%;

				div.imagescontainer{
					display: none;
				}

				div.logo {
					&.mobile{
						display: none;
					}
					margin-bottom: 15px;
					overflow: hidden;
					height: 155px;
					width: 375px;
					display: flex;
					justify-content: center;
					align-items: center;

					img {
						width: 320px;
						transition: $section1_logo_transition_duration;
						transform: translateY(170px) scale(0.8);
						opacity: 0;
					}

					&.logoanimate {
						img {
							opacity: 1;
							transform: translateY(0) scale(1);
						}
					}
				}

				div.laptop {
					margin-bottom: 15px;
					width: 75%;
					height: 114px;
					text-align: center;

					img {
						height: 100%;
					}
				}

				div.stores {
					width: 80%;
					height: 60px;
					display: flex;
					align-items: flex-start;

					a {
						height: 100%;
						width: 50%;
						display: inline-flex;

						img {
							border: none;
							width: 100%;
						}
					}
				}

				img.maclogo {
					width: 67%;
					height: 63px;
					margin-top: 10px;
				}
			}

			div.left {
				position: relative;
				width: 60%;
				height: 100%;

				img.kidslogo {
					position: absolute;
					bottom: -110px;
					width: 580px;
					left: 0;
					z-index: 5;
				}

				div.imagescontainer {
					position: absolute;
					width: 550px;
					height: 550px;
					bottom: -100px;
					left: 20px;

					div.firstcircle {
						width: 100%;
						height: 100%;
						border-radius: 50%;
						background-color: rgba(255, 255, 255, 0.1);
						padding: 30px;

						div.secondcircle {
							padding: 30px;
							width: 100%;
							height: 100%;
							border-radius: 50%;
							background-color: rgba(255, 255, 255, 0.1);

							div.thirdcircle {
								padding: 30px;
								width: 100%;
								height: 100%;
								border-radius: 50%;
								background-color: rgba(255, 255, 255, 0.1);

								div.forthcircle {
									display: flex;
									align-items: center;
									justify-content: center;
									width: 100%;
									height: 100%;
									border-radius: 50%;
									background-color: rgba(255, 255, 255, 0.1);

									img.pics {
										position: absolute;
										left: 150px;
										top: 150px;

										&:nth-child(1) {}

										&:nth-child(2) {}

										&:nth-child(3) {
											width: 150px;
										}

										&:nth-child(4) {
											width: 200px;
										}

										&:nth-child(5) {}

										&:nth-child(6) {}

										&:nth-child(7) {}
									}

									img#earth {
										position: relative;
										z-index: 1;
										width: 90%;
										height: 90%;
										animation: earthrotations 100s infinite linear;
									}

									@keyframes earthrotations {
										0% {
											transform: rotateZ(0deg);
										}

										100% {
											transform: rotateZ(360deg);
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#sectionlast{
		height: 480px;
		padding: 15px 0;
		div.inner{
			div.elementscontainer{
				div.right{
					div.logo{
						width: 100%;
						margin-bottom: 0px;
						img{
							width: 300px;
						}
					}
					div.laptop{
						width: 80%;
						margin-bottom: 0;
						display: flex;
						align-tems: center;
						img{
							height: auto;
							width: 100%;
						}
					}
					div.maclogo{
						margin-top: 0;
					}
				}
				div.left{
					div.imagescontainer{
						transform-origin: 0% 50%;
						transform: scale(0.8);
					}
					img.kidslogo{
						bottom: -70px;
						width: 495px;
					}
				}
			}
		}
	}
}


/*=============================================
=            Mobile media query               =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section#sectionlast{
		height: auto;
		border-top-right-radius : 10px;
		border-top-left-radius : 10px;
		padding: 5px;
		div.inner{
			border: 2px dashed white;
			border-top-right-radius : 10px;
			border-top-left-radius : 10px;
			div.elementscontainer{
				div.left{
					display: none;
				}
				div.right{
					width: 100%;
					div.logo{
						&.normal{
							display: none;
						}
						&.mobile{
							display: flex;
							img{
								transform: translateY(0) scale(1);
								opacity: 1;
							}
						}
					}
					img.maclogo {
						margin: 10px 0;
						width: 65%;
					}
					div.laptop{
						margin: 10px 0;
					}
					div.imagescontainer{
						background-image: url(../images/sectionlast/earth.png);
						background-size: cover;
						margin-bottom: 30px;
						display: block;
						position: relative;
						width: 300px;
						height: 300px;
						img.kidslogo{
							width: 100%;
							left: 0;
							right: 0;
							top: 50%;
							bottom: 0px;
							margin: auto;
							position: absolute;
							z-index: 2;
						}
					}
				}
			}
		}
	}
}