$questionsAnimationsTime: 1.5s;

section#section4 {
	width: 100vw;
	height: 300px;
	background-color: #ed165a;
	padding-top: 10px;
	margin-bottom: 5px;

	> div.inner {
		border-top: 3px dashed white;
		height: 100%;

		div.elementscontainer {
			a {
				text-decoration: none;
				color: black;
			}

			display: flex;
			justify-content: space-between;

			div.right {
				width: 50%;
				height: 100%;
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;

				div.logo{
					display: none;
				}

				div.text {
					display: inline-block;
					color: white;
					width: 95%;

					h1 {
						font-family: isra;
						color: white;
						text-align: center;
						font-size: 3em;
						font-weight: normal;
						margin: 0;
					}

					p {
						font-size: 0.7em;
						line-height: 2.2em;
						font-family: jf;
						color: white;
						text-align: right;
					}
				}

				div.questionimage{
					display: none;
				}

				div.stores {
					width: 100%;
					display: flex;
					justify-content: center;

					a {
						display: inline-block;
						width: 30%;
						margin-top: 20px;

						img {
							width: 100%;
						}
					}
				}
			}

			div.left {
				height: 100%;
				width: 50%;
				text-align: center;

				div.questions {
					height: 266px;
					margin: 0 auto;
					margin-top: 10px;
					position: relative;
					bottom: -30px;
					width: 400px;
					z-index: 2;
					perspective: 1200px;

					img {
						left: 0;
						top: 0;
						position: absolute;
						transform-origin: 100% 100%;
						width: 100%;
						transform-style: preserve-3d;

						&.active {
							transform: rotateX(360deg);
							opacity: 1;
							z-index: 1;
							transition: $questionsAnimationsTime opacity cubic-bezier(0, 1, 0, 1), $questionsAnimationsTime transform;
						}

						&.next {
							transform: rotateX(278deg);
							opacity: 0;
							z-index: 2;
						}

						&.prev {
							transition: $questionsAnimationsTime opacity cubic-bezier(1, 0, 1, 0), $questionsAnimationsTime transform;
							transform: rotateX(450deg);
							opacity: 0;
							z-index: 0;
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#section4 {
		height: 250px;

		div.inner {
			div.elementscontainer {
				div.right {
					div.text {
						padding: 0 20px;

						h1 {
							font-size: 2.5em;
						}

						p {
							font-size: 0.6em;
							line-height: 1.8em;
						}
					}

					div.stores {
						align-items: center;
						flex-grow: 1;

						a {
							margin: 0;
						}
					}
				}

				div.left {
					div.questions {
						height: 206px;
						width: 360px;
						bottom: -15px;
						right: -25px;
					}
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section#section4 {
		background: none;
		background-image: url("../images/section1/background.svg"),linear-gradient(to top, #ffc140 0%, #ffc70c 50%, #ffc140 100%);
		height: auto;
		padding: 5px;
		border-top-right-radius: 10px;
		border-top-left-radius: 10px;

		div.inner {
			border-top-right-radius: 10px;
			border-top-left-radius: 10px;
			border: 2px dashed white;

			div.elementscontainer {
				div.right {
					padding: 10px;
					width: 100%;

					div.logo {
						display: block;
						width: 65%;
						margin: 0 auto;

						img {
							width: 100%;
						}
					}

					div.text {
						padding: 0;
						width: 80%;

						h1 {
							color: black;
						}

						p {
							color: black;
							font-size: 0.7em;
						}
					}


					div.questionimage{
						width: 90%;
						display: block;
						margin-top: 10px;
						img{
							width: 100%;
						}
					}

					div.stores {
						display: none;
						margin: 10px 0;
					}
				}

				div.left {
					display: none;
				}
			}
		}
	}
}