$section1_footer_transition_duration: 0.5s;
$section1_logo_transition_duration: 1s;
$section1_stage_transition_duration: 0.75s;
$create_light: #9B3A96;
$create_dark: #7F287F;
$evaluate_light: #03a697;
$evaluate_dark: #01746a;
$analyze_light: #ED247C;
$analyze_dark: #C2206E;
$apply_light: #8DC541;
$apply_dark: #76A440;
$understand_light: #F37046;
$understand_dark: #C55135;
$remember_light: #2CAAE1;
$remember_dark: #258CB0;

section#section1 {
	width: 100vw;
	min-height: 768px;
	position: relative;
	overflow: hidden;
	display: flex;
	flex-direction: column;

	div.header {
		height: 40px;
		font-family: jf;
		font-size: 0.7em;
		transition: $section1_footer_transition_duration opacity, $section1_footer_transition_duration top;

		&.hide {
			opacity: 0;
		}

		div.elementscontainer {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: space-between;

			a {
				display: inline-block;
				height: 100%;
				color: black;
				text-decoration: none;
				margin: 0 5px;

				p {
					margin: 0;
				}
			}

			div.btn {
				height: 100%;
				display: inline-flex;
				justify-content: center;
				align-items: center;
				border-radius: 30px;
				margin: 0;
				padding: 0px 30px;
				transition: $section1_footer_transition_duration background-color;
			}

			div.user {
				display: none;
				padding: 0 10px;
				z-index: 5;
				align-self: flex-start;
				width: 240px;
				height: 70px;
				background-color: #343746;
				border-bottom-left-radius: 10px;
				border-bottom-right-radius: 10px;
				justify-content: space-between;
				align-items: center;

				div.image {
					width: 30%;
					height: 100%;
					display: flex;
					justify-content: center;
					align-items: center;

					div.userimage {
						background-size: 105% 105%;
						width: 55px;
						height: 55px;
						border-radius: 50%;
						border: 4px solid white;
						background-image: url("../images/global/user.svg");
					}
				}

				div.info {
					width: 70%;
					height: 100%;
					color: white;
					display: inline-flex;
					flex-direction: column;
					justify-content: center;

					p {
						line-height: 1.2em;
						word-wrap: break-word;
						text-align: center;

						a {
							color: #ffc140;
							cursor: pointer;
						}

						&:first-child {
							font-weight: bold;
						}
					}
				}
			}

			div.right {
				width: 50%;
				height: 100%;
				position: relative;

				div.btn {
					padding: 0px 15px;
				}
			}

			div.left {
				width: 50%;
				text-align: center;

				div.userauth {
					display: inline-block;
					background-color: white;
					bottom: -15px;
					border: 5px solid #ffc140;
					border-radius: 30px;
					position: relative;
					padding: 10px 0;
					left: 0;
					right: 0;
					margin: 0 auto;

					a {
						&:first-child {
							div.btn {
								border-bottom-left-radius: 0;
								border-top-left-radius: 0;
							}
						}

						&:last-child {
							cursor: pointer;

							div.btn {
								border-top-right-radius: 0;
								border-bottom-right-radius: 0;
							}
						}
					}
				}
			}
		}

		&.loggedin {
			div.elementscontainer {
				div.user {
					display: flex;
					width: 240px;
				}

				div.right {
					margin-right: 30px;
					width: 58%;
				}

				div.left {
					display: none;
				}
			}
		}
	}

	div.content {
		height: 600px;
		background-image: url("../images/section1/background.svg"), linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
		background-image: url("../images/section1/background.svg"), -webkit-linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
		background-image: url("../images/section1/background.svg"), -moz-linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
		background-image: url("../images/section1/background.svg"), -ms-linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
		background-image: url("../images/section1/background.svg"), -o-linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);

		div.elementscontainer {
			display: flex;
			justify-content: space-between;

			div.right {
				width: 50%;
				position: relative;

				div.sectionicons {
					&.inner {
						display: none;
					}
				}

				img#ballon {
					position: absolute;
					top: 80px;
					width: 170px;
					right: 30%;
					z-index: 2;
					cursor: pointer;
					animation: ballon 120s alternate infinite;
				}

				img.cloud {
					position: absolute;
				}

				@keyframes cloudone {
					0% {
						transform: translate(0%, 0%);
					}

					50% {
						transform: translate(-40%, -25%);
					}

					100% {
						transform: translate(10%, 20%);
					}
				}

				@keyframes cloudtwo {
					0% {
						transform: translate(0%, 0%);
					}

					50% {
						transform: translate(20%, -50%);
					}

					100% {
						transform: translate(-20%, -40%);
					}
				}

				@keyframes cloudthree {
					0% {
						transform: translate(0%, 0%);
					}

					50% {
						transform: translate(10%, -70%);
					}

					100% {
						transform: translate(-10%, 90%);
					}
				}

				@keyframes ballon {
					0% {
						transform: translate(0%, 0%);
					}

					25% {
						transform: translate(40%, -20%);
					}

					50% {
						transform: translate(10%, 30%);
					}

					75% {
						transform: translate(0%, 0%);
					}

					100% {
						transform: translate(-10%, 10%);
					}
				}

				img#cloud1 {
					animation: cloudone 120s alternate infinite;
					top: 120px;
					right: 30px;
					width: 220px;
					z-index: 5;
				}

				img#cloud2 {
					animation: cloudtwo 120s alternate infinite;
					top: 40px;
					right: 180px;
					width: 180px;
					z-index: 1;
				}

				img#cloud3 {
					animation: cloudthree 120s alternate infinite;
					top: 140px;
					right: 310px;
					width: 150px;
					z-index: 1;
				}

				div.kidscontainer {
					width: 410px;
					height: 330px;
					position: absolute;
					bottom: 0;
					left: 0;
					right: 0;
					margin: 0 auto;

					#girl,
					#boy {
						position: absolute;
						bottom: 0px;
					}

					#girl {
						right: 0;
						z-index: 4 !important;
						width: 236px;
					}

					#boy {
						width: 278px;
						right: 125px;
						z-index: 3 !important;
					}
				}
			}

			div.left {
				height: 100%;

				&.mobile {
					display: none;
				}

				position: relative;
				display: flex;
				justify-content: space-between;
				align-items: center;
				flex-direction: column;
				padding-top: 40px;
				width: 50%;

				div.tabletmask {
					transform-origin: center top;
					width: 398px;
					overflow: hidden;
					height: 270px;

					div.tablet {
						width: 100%;
						height: 100%;
						transform: translateY(140%);
						position: relative;

						div.tabletborder {
							text-align: center;
							width: 100%;
							height: 100%;
							position: relative;
							z-index: 2;

							div.taxonomyswiper {
								position: absolute;
								width: 354px;
								height: 234px;
								left: 18px;
								top: 15px;
								border-radius: 10px;
								overflow: hidden;

								div.taxonomy-slide {
									opacity: 0;
									left: 0;
									top: 0;
									position: absolute;
									width: 100%;
									height: 100%;
									border-radius: 10px;
									transform-origin: center center;
									transition: 0.5s;

									div.logo {
										background-color: white;
										width: 100%;
										z-index: 1;
										overflow: hidden;
										height: 100%;
										transform: scaleX(0);
										display: flex;
										justify-content: center;
										align-items: center;

										img {
											width: 320px;
											transition: $section1_logo_transition_duration;
										}
									}

									&.active {
										opacity: 1;
									}

									&.taxonomytext {
										padding: 0 25px;
										line-height: 2em;
										font-family: jf;
										justify-content: center;
										align-items: center;
										display: flex;
										font-size: 1em;
										color: white;
										transition: 0.25s;

										ul {
											width: 100%;
											text-align: right;
										}

										&.create {
											background-color: rgba($create_light, 0.5);
										}

										&.evaluate {
											background-color: rgba($evaluate_light, 0.5);
										}

										&.analyze {
											background-color: rgba($analyze_light, 0.5);
										}

										&.apply {
											background-color: rgba($apply_light, 0.5);
										}

										&.understand {
											background-color: rgba($understand_light, 0.5);
										}

										&.remember {
											background-color: rgba($remember_light, 0.5);
										}
									}
								}
							}
						}
					}
				}

				div.taxonomycontainer {
					width: 480px;
					position: absolute;
					bottom: 0px;
					left: 0px;
					right: 0px;
					margin: 0 auto;
					transform-origin: center bottom;

					div.taxonomy {
						position: relative;
						bottom: 104px;
						width: 100%;
						height: 287px;

						div.level {
							transform: scaleY(0);
							margin: 0 auto;
							width: 100%;
							position: relative;
							perspective: 200px;
							flex-direction: column;
							justify-content: center;
							display: flex;

							> div {
								width: 100%;
								height: 33px;
							}

							div.face {
								p {
									height: 100%;
									display: flex;
									align-items: center;
									justify-content: center;
									color: white;
									font-family: isra;

									span.card {
										display: inline-block;
										perspective: 300px;
										position: relative;
										width: 100%;
										height: 130%;
										transform-style: preserve-3d;
										backface-visibility: hidden;

										span {
											font-size: 1.4em;
											display: inline-block;
											width: 100%;
											height: 100%;
											position: absolute;
											backface-visibility: hidden;
											z-index: 2;
											text-align: center;
											transition: 0.5s;
											transform-style: preserve-3d;

											&.en {
												position: absolute;
												z-index: 1;
												transform: rotateX(0deg);
											}

											&.ar {
												transform: rotateX(-180deg);
												display: flex;
												justify-content: center;
											}
										}
									}

									&.changeArabic {
										span.card {
											span {
												&.en {
													transform: rotateX(180deg);
												}

												&.ar {
													transform: rotateX(0deg);
												}
											}
										}
									}
								}
							}

							div.side {
								position: relative;
								bottom: -0.5px;
								transform-style: preserve-3d;
								transform-origin: bottom;
								transform: rotateX(60deg);
								width: 100.1%;
							}

							&.create {
								bottom: -100px;
								width: 52%;

								> div {
									&.face {
										background-color: $create_light;
									}

									&.side {
										background-color: $create_dark;
									}
								}
							}

							&.evaluate {
								bottom: -79px;
								width: 59.4%;

								> div {
									&.face {
										background-color: $evaluate_light;
									}

									&.side {
										background-color: $evaluate_dark;
									}
								}
							}

							&.analyze {
								bottom: -58px;
								width: 67.5%;

								> div {
									&.face {
										background-color: $analyze_light;
									}

									&.side {
										background-color: $analyze_dark;
									}
								}
							}

							&.apply {
								bottom: -38px;
								width: 77.3%;

								> div {
									&.face {
										background-color: $apply_light;
									}

									&.side {
										background-color: $apply_dark;
									}
								}
							}

							&.understand {
								bottom: -19px;
								width: 87.5%;

								> div {
									&.face {
										background-color: $understand_light;
									}

									&.side {
										background-color: $understand_dark;
									}
								}
							}

							&.remember {
								bottom: 0;
								width: 100%;

								> div {
									&.face {
										background-color: $remember_light;
									}

									&.side {
										background-color: $remember_dark;
									}
								}
							}
						}
					}

					div.taxonomymask {
						position: absolute;
						width: 100%;
						bottom: 0;
						left: 0;
						height: 280px;

						div.level {
							cursor: pointer;
							position: absolute;
							width: 100%;
							height: 46px;
							left: 0;
							right: 0;
							margin: 0 auto;

							&.create {
								top: 7px;
								width: 52%;
							}

							&.evaluate {
								top: 53px;
								width: 59.4%;
							}

							&.analyze {
								top: 98px;
								width: 67.5%;
							}

							&.apply {
								top: 145px;
								width: 77.3%;
							}

							&.understand {
								top: 191px;
								width: 87.5%;
							}

							&.remember {
								top: 236px;
								width: 100%;
							}
						}
					}
				}
			}
		}
	}

	div.line {
		background-color: #2caae1;
		height: 25px;
		padding-top: 5px;

		div.inner {
			border-top: 2px dashed white;
			width: 100%;
			height: 100%;
		}
	}

	div.footer {
		flex-grow: 1;
		height: 200px;
		background: linear-gradient(180deg, rgba(0, 0, 0, 0.15) 0%, rgba(0, 0, 0, 0) 100%);

		div.elementscontainer {
			display: flex;
			justify-content: center;
			align-items: center;

			div.text {
				color: black;
				margin-top: 50px;

				p {
					line-height: 1.6em;
					font-size: 0.75em;
					text-align: center;
					font-family: jf;

					&:nth-child(2) {
						font-size: 0.9em;
					}
				}
			}
		}
	}

	div.sectionicons {
		position: fixed;
		pointer-events: none;
		display: flex;
		justify-content: space-between;
		width: 400px;
		height: 55px;
		right: 50%;
		margin-right: -200px;
		top: 680px;
		z-index: 500;
		transform-origin: center right;
		transition: 1s;

		a {
			display: inline-block;
			width: 55px;
			height: 55px;

			div.toScale {
				height: 100%;
				width: 100%;
				transform-origin: 50% 100%;
				transition: 0.5s;

				div.toTranslate {
					height: 100%;
					width: 100%;

					img {
						transition: 0.5s;
						height: 100%;
						width: 100%;
						pointer-events: auto;
						cursor: pointer;
						border: none;
					}
				}
			}

			&.active {
				div.toScale {
					transform: scale(1.3);
				}
			}

			&:first-child, &:last-child {
				div.toScale {
					transform: scale(1);
				}
			}
		}

		&.toside {
			transform: rotateZ(-90deg);
			right: 240px;
			width: 380px;
			top: 50%;
			margin-top: -120px;

			div.toScale {
				div.toTranslate {
					img {
						transform: rotateZ(90deg);
					}
				}
			}

			&.return {
				right: 50%;
				top: 98.5%;
				transform: rotateZ(0deg);

				div.toScale {
					div.toTranslate {
						img {
							transform: rotateZ(0deg);
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#section1 {
		div.header {
			&.loggedin {
				div.elementscontainer {
					div.user {
						width: 240px;
					}
				}

				div.right,
				div.left {
					a {
						div.btn {
							padding: 0px 10px;
						}
					}
				}

				div.left {
					display: none;
				}
			}
		}

		div.sectionicons.outer {
			display: none;
		}

		div.content {
			div.elementscontainer {
				div.left {
					div.tabletmask {
						height: 240px;
						width: 350px;

						div.tablet {
							div.tabletborder {
								div.taxonomyswiper {
									width: 308px;
									height: 204px;

									div.taxonomy-slide {
										&.taxonomylogo {
											div.logo {
												img {
													width: 100%;
												}
											}
										}

										&.taxonomytext {
											padding: 0 10px;
										}
									}
								}

								> img {
									width: 100%;
								}
							}
						}
					}

					div.taxonomycontainer {
						width: 350px;
						right: -8px;
					}
				}

				div.right {
					img#ballon, img#cloud1, img#cloud2, img#cloud3 {
						animation: initial;
					}

					div.sectionicons {
						&.inner {
							display: flex;
							position: absolute;
							left: 0;
							margin: 0 auto;
							right: 0;
							top: 240px;
							width: 350px;

							a div.toScale {
								div.toTranslate {
									transform: translateY(0px);

									img {}
								}
							}
						}
					}

					img#ballon {
						top: 40px;
						width: 120px;
					}

					img#cloud1 {}

					img#cloud2 {
						top: 40px;
						right: 6%;
						width: 190px;
					}

					img#cloud3 {
						top: 120px;
						right: 45%;
					}

					div.kidscontainer {
						width: 321px;
						height: 270px;

						img#boy {
							width: 208px;
							right: 95px;
						}

						img#girl {
							width: 186px;
						}
					}
				}
			}
		}

		div.footer {
			div.inner {
				div.elementscontainer {
					div.text {
						margin-top: 0;
						padding: 0 40px;

						p {
							&:nth-child(2) {
								line-height: 2em;
							}
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section#section1 {
		background-color: #2caae1;
		div.header {
			display: none;
			div.elementscontainer {
			}
		}

		div.content {
			padding-top: 35px;
			height: auto;

			div.elementscontainer {
				div.right {
					display: none;
				}

				div.left {
					&.normal {
						display: none;
					}

					&.mobile {
						display: flex;
						height: auto;
						width: 100%;

						div.taxonomycontainer {
							position: relative;
							width: 95%;
							right: 0;

							div.taxonomy {
								div.level {
									transform: scaleY(1);
								}
							}
						}

						div.tabletmask {
							height: auto;
							min-height: 200px;
							transform: scale(1);
							width: 90%;

							div.tablet {
								transform: translateY(0%);

								div.tabletborder {
									> img {
										width: 100%;
									}

									div.taxonomyswiper {
										width: 90%;
										height: 86%;
										left: 4%;
										top: 6%;

										div.taxonomy-slide {
											&.taxonomylogo {
												div.logo {
													width: 100%;
													transform: scaleX(1);
													background-color: rgba(255, 255, 255, 0);

													img {
														width: 85%;
													}
												}
											}

											&.taxonomytext {
												padding: 0 10px;

												p {
													height: 100%;
													display: flex;
													align-items: center;
													line-height: 10vw;
													font-size: 5vw;
												}

												ul {
													height: 100%;
													line-height: 10vw;
													display: flex;
													flex-direction: column;
													justify-content: center;
													font-size: 6vw;
													padding-right: 8vw;
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}

		div.line{
			display: none;
		}

		div.footer {
			background: none;
			background-color: #2caae1;
			height: auto;
			div.elementscontainer {
