footer#footer {
	width: 100vw;
	div.upper {
		padding: 50px 0;
		div.elementscontainer {
			div.footerrow{
				display: flex;
				a{
					&.main{
						p.link{
							span.colored{
								background-color: #ee145b;
								color: white;
							}
						}
					}
					display: inline-flex;
					color: black;
					text-decoration: none;
					p.link{
						width: 200px;
						padding: 5px;
						font-family: isra;
						span{
							border-radius: 20px;
							transition: 1s;
							display: inline-block;
							padding: 5px 20px;
						}
					}
				}
			}
		}
	}
	div.lower {
		padding: 20px 0;
		background-color: #dddfde;
		div.elementscontainer {
			p {
				text-align: center;
				font-family: isra;
			}
		}
	}
}


/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	footer#footer{
		div.upper{
			padding: 30px 0;
		}
	}
}

/*=============================================
=            Mobile media query              =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	footer#footer{
		div.upper{
			display: none;
			padding: 10px 0;
			div.elementscontainer{
				padding: 10px;
				div.footerrow{
					a{
						width: 33%;
						p.link{
							width: 100%;
							span{
								padding-left: 0;
							}
						}
						&.main{
							p.link{
								span{
									padding: 5px 20px;
								}
							}
						}
					}
				}
			}
		}
		div.lower{
			padding: 10px 0;
		}
	}
}