div#login {
	font-family: jf;
	padding: 0 !important;

	div.modal-dialog {
		text-align: center;
		margin-top: 20%;
		width: 400px;

		div.modal-content {
			box-shadow: 0px 9px 15px 9px rgba(0, 0, 0, 0.4);
			height: 290px;
			border-radius: 20px;

			div.text {
				position: relative;
				bottom: 48px;

				div.logo {
					background-color: #2caae1;
					display: inline-block;
					padding: 5px;
					border-radius: 20px;

					div.inner {
						display: flex;
						align-items: center;
						padding: 5px 10px;
						border: 3px solid white;
						border-radius: 20px;

						div.mask {
							height: 60px;
							overflow: hidden;
							img {
								width: 165px;
							}
						}
					}
				}

				h1 {
					font-size: 0.9em;
					text-align: center;
				}

				> img {
					cursor: pointer;
					width: 65%;
				}
			}
		}
	}
}