section#section5 {
	position: relative;
	width: 100vw;

	div.upper {
		height: 250px;
		background-image: url("../images/section1/background.svg"), linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
		background-image: url("../images/section1/background.svg"), -webkit-linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
		background-image: url("../images/section1/background.svg"), -moz-linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
		background-image: url("../images/section1/background.svg"), -ms-linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
		background-image: url("../images/section1/background.svg"), -o-linear-gradient(to top, rgb(255, 193, 64) 0%, rgb(255, 199, 12) 50%, rgb(255, 193, 64) 100%);
	}

	div.lower {
		height: 250px;
	}

	div.content {
		width: 100%;
		height: 500px;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;

		h1 {
			text-align: center;
			font-family: isra;
			font-weight: normal;
			font-size: 2.5em;
			margin-bottom: 0;
			margin-top: 0.4em;
		}

		p {
			text-align: center;
			font-family: jf;
			font-size: 0.8em;
		}

		div#kidsSwiper {
			margin-top: 15px;
			padding-top: 15px;
			padding-bottom: 15px;
			width: 100%;
			height: 260px;

			div.kidSwiper {
				transition: 0.5s;

				&.swiper-slide-active {
					transform: scale(1.2);

					div.upperPic {
						div.outerCircle {
							&:after {
								opacity: 0;
							}
						}
					}
				}

				padding: 10px;
				height: 100%;
				width: 20%;

				div.upperPic {
					height: 75%;

					div.outerCircle {
						width: 150px;
						height: 150px;
						margin: 0 auto;
						border-radius: 50%;
						background-color: white;
						padding: 5px;
						position: relative;

						&:after {
							transition: 0.5s;
							width: 150px;
							height: 150px;
							border-radius: 50%;
							content: '';
							position: absolute;
							top: 0;
							left: 0;
							background-color: white;
							opacity: 0.7;
						}

						div.innerCircle {
							position: relative;
							border-radius: 50%;
							width: 100%;
							height: 100%;
							overflow: hidden;

							&:before {
								border-radius: 50%;
								content: '';
								position: absolute;
								width: 100%;
								height: 100%;
								box-shadow: inset 0px 0px 15px 3px rgba(0, 0, 0, 0.5);
							}

							img {
								width: 100%;
							}
						}
					}
				}

				div.shadow {
					perspective: 1000;
					height: 20%;
					display: flex;
					align-items: flex-end;

					div.block {
						width: 40%;
						height: 40%;
						background-color: rgba(0, 0, 0, 0.35);
						border-radius: 50%;
						margin: 0 auto;
						filter: blur(6px);
					}
				}
			}
		}

		div.swipertitle {
			margin-top: 20px;
			text-align: center;

			> div {
				display: inline-block;
				background-color: #ffc52f;
				font-family: jf;
				padding: 10px 35px;
				border-radius: 30px;
			}
		}
	}

	div.stats {
		width: 100%;

		h1 {
			text-align: center;
			font-family: isra;
			font-weight: normal;
			font-size: 2.5em;
			margin: 0;
			margin-bottom: 0.4em;
		}

		div.statsnumbers {
			flex-wrap: wrap;
			display: flex;
			justify-content: space-around;
			align-items: center;
			margin-bottom: 2em;

			div.stat {
				line-height: 1.8em;
				font-family: jf;
				width: 230px;
				border-radius: 10px;
				position: relative;

				div.up, div.down {
					padding: 3px;
				}

				div.up {
					background-color: #179dd9;
					border-radius: 10px;

					p {
						text-align: center;
						color: white;
						border: 1px dashed white;
						border-radius: 10px;
					}
				}

				div.down {
					position: absolute;
					background-color: white;
					border: 3px solid #179dd9;
					height: 80%;
					width: 70%;
					left: 0;
					right: 0;
					margin: auto;
					bottom: -35px;

					p {
						height: 100%;
						width: 100%;
						display: flex;
						align-items: center;
						justify-content: center;
						color: black;
					}
				}

				&:nth-child(1) {
					div.up{
						background-color: #179dd9;
					}
					div.down{
						border-color: #179dd9;
					}
				}

				&:nth-child(2) {
					div.up{
						background-color: #ed237c;
					}
					div.down{
						border-color: #ed237c;
					}
				}

				&:nth-child(3) {
					div.up{
						background-color: #8cc640;
					}
					div.down{
						border-color: #8cc640;
					}
				}

				&:nth-child(4) {
					div.up{
						background-color: #fdc026;
					}
					div.down{
						border-color: #fdc026;
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#section5 {
		div.stats {
			div.elementscontainer {
				div.statsnumbers {
					margin-bottom: 0px;

					div.stat {
						margin-bottom: 2em;
						width: 35%;
					}
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query               =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section#section5 {
		padding-bottom: 20px;
		padding-top: 125px;
		div.upper{
			height: 125px;
			&:before{
				height: 125px;
				content: "";
				display: block;
			}
		}
		div.lower{
			height: 150px;
		}
		div.content {
			height: 400px;
			div.elementscontainer {
				h1 {
					margin-top: 0em;
				}
				div#kidsSwiper{
					margin-top: 30px;
				}
				div.swipertitle{
					display: none;
				}
			}
		}

		div.stats{
			div.elementscontainer{
				h1{
					font-size: 2.2em;
				}
				div.statsnumbers{
					font-size: 0.7em;
					div.stat{
						width: 48%;
						div.down{
							bottom: -24px;
						}
					}
				}
			}
		}
	}
}