section#section2 {
	padding-top: 50px;
	width: 100vw;
	position: relative;
	overflow: hidden;
	background: url(../images/global/backgroundgrey.svg);

	div.stages {
		height: 150px;
		margin-bottom: 20px;

		&.mobile {
			display: none;
		}

		a {
			text-decoration: none;
			color: black;
		}

		&.stagesanimate {
			div.elementscontainer {
				div.stage {
					transform: rotateX(0);
					opacity: 1;
					transition: transform $section1_stage_transition_duration ease, opacity $section1_stage_transition_duration cubic-bezier(0, 1.01, 0, 1.01);

					&.prim {
						transition-delay: 0.5s;
					}

					&.prep {
						transition-delay: 0s;
					}

					&.sec {
						transition-delay: 0.25s;
					}
				}
			}
		}

		div.elementscontainer {
			text-align: center;
			display: flex;
			justify-content: space-around;
			align-items: center;
			perspective: 600px;
			padding: 0 60px;

			div.stage {
				opacity: 0;
				transform-style: preserve-3d;
				transform-origin: bottom;
				transition: transform $section1_stage_transition_duration ease, opacity $section1_stage_transition_duration cubic-bezier(0, 0.01, 1, -0.01);
				transform: rotateX(94deg);
				cursor: pointer;
				width: 279px;
				height: 148px;
				padding: 10px;
				box-shadow: 0px 13px 19px -5px rgba(50, 50, 50, 0.4);

				&.prim {
					background-color: #00aeef;
					transition-delay: 0s;
				}

				&.prep {
					background-color: #ee145b;
					transition-delay: 0.5s;
				}

				&.sec {
					background-color: #8dc63e;
					transition-delay: 0.25s;
				}

				div.dashed {
					font-family: jf;
					line-height: 1.3em;
					font-size: 1.9em;
					border: 3px dashed white;
					width: 100%;
					height: 100%;
					padding: 5px;
					color: white;
					display: flex;
					align-items: center;
					justify-content: center;
				}
			}
		}
	}

	div.upper {
		position: relative;
		height: 450px;

		div.elementscontainer {
			display: flex;

			> div {
				width: 50%;

				&.right {
					position: relative;

					div.text {
						margin: 0 auto;
						width: 500px;

						img.logo{
							display: none;
						}

						h1 {
							font-family: isra;
							text-align: center;
							margin: 0 0px 10px;
							font-size: 2.8em;
							font-weight: normal;
						}

						p {
							font-family: jf;
							width: 70%;
							text-align: justify;
							margin: 0 auto;
							font-size: 1em;
							line-height: 1.8em;
						}
					}

					div.tubes {
						perspective: 1200px;
						height: 150px;
						position: absolute;
						bottom: 0px;
						left: 0;
						display: flex;
						align-items: flex-end;

						img {
							transform-style: preserve-3d;
							transform-origin: bottom;

							&:not(.circle) {
								transform: rotateX(-90deg);
							}

							&.circle {
								transform: scale(0);
								transform-origin: center center;

								&.hide {
									width: 0 !important;
								}
							}

							&#flask {
								position: relative;
								bottom: -4px;
							}

							&#bigflask {
								position: relative;
								bottom: -4px;
							}

							&#tubes {
								position: relative;
								bottom: 0;
							}

							&#beaker {
								position: relative;
								bottom: -1px;
							}

							&.circle {
								position: absolute;

								&:nth-child(1) {
									bottom: 53px;
									right: 35px;
									width: 17px;
								}

								&:nth-child(2) {
									bottom: 83px;
									right: 61px;
									width: 11px;
								}

								&:nth-child(3) {
									bottom: 84px;
									right: 77px;
									width: 7px;
								}

								&:nth-child(4) {
									bottom: 115px;
									right: 79px;
									width: 7px;
								}

								&:nth-child(5) {
									bottom: 104px;
									right: 80px;
									width: 5px;
								}

								&:nth-child(6) {
									bottom: 96px;
									right: 95px;
									width: 5px;
								}

								&:nth-child(7) {
									bottom: 105px;
									right: 59px;
									width: 5px;
								}
							}
						}
					}
				}

				&.left {
					text-align: center;

					img {
						height: 100%;
					}
				}
			}
		}
	}

	div.lower {
		position: relative;
		height: 200px;
		background-color: #00aeef;
		padding-top: 10px;

		div.inner {
			border-top: 3px dashed white;
			height: 100%;

			div.elementscontainer {
				display: flex;

				> div {
					width: 50%;

					&.right {
						position: relative;

						img {
							position: absolute;
							bottom: 0;
							right: 60px;
							height: 250px;
						}
					}

					&.left {
						display: flex;
						align-items: center;
						justify-content: center;

						div.logo {
							&.mobile {
								display: none;
							}
							overflow: hidden;
							height: 155px;

							img {
								width: 320px;
								transition: $section1_logo_transition_duration;
								transform: translateY(170px) scale(0.8);
								opacity: 0;
							}

							&.logoanimate {
								img {
									opacity: 1;
									transform: translateY(0) scale(1);
								}
							}
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#section2 {
		padding-top: 50px;

		div.stages {
			margin-bottom: 0;

			div.elementscontainer {
				padding: 0 40px;
				div.stage {
					width: 200px;
					height: 118px;

					a {
						div.dashed {
							font-size: 1.2em;
						}
					}
				}
			}
		}

		div.upper {
			height: 300px;

			div.elementscontainer {
				div.right {
					width: 100%;

					div.text {
						width: 760px;

						p {
							width: 80%;
						}
					}

					div.tubes {
						left: 50px;
					}
				}

				div.left {
					display: none;
				}
			}
		}

		div.lower {
			div.inner {
				div.elementscontainer {
					div.right {
						img {
							left: 0;
							right: 0;
							margin: auto;
						}
					}
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section#section2 {
		background: none;
		position: relative;
		bottom: 1px;
		padding-top: 0;

		div.stages {
			&.normal {
				display: none;
			}

			&.mobile {
				height: auto;
				display: block;

				div.elementscontainer {
					padding: 0 5px;
					flex-wrap: wrap;

					div.stage {
						margin: 20px 0;
						transform: rotateX(0);
						opacity: 1;
						width: 30%;
						padding: 5px;
						height: auto;
						div.dashed{
							font-size: 0.8em;
							border: 2px dashed white;
							padding: 10px 5px;
						}
					}
					img{
						width: 80%;
					}
					&:last-child{
						border-bottom: 1px solid rgba(190, 190, 192, 0.75);
						margin: 0 20px;
					}
				}
			}
		}

		div.upper {
			height: auto;

			div.elementscontainer {
				div.right {
					background-color: #2caae1;
					padding: 10px;
					div.text {
						padding: 5px;
						border-top-right-radius: 10px;
						border-top-left-radius: 10px;
						width: 100%;
						color: white;
						border: 2px dashed white;

						img.logo{
							display: block;
							margin: 0 auto;
							width: 65%;
						}

						h1 {
							font-size: 2.2em;
						}

						p {
							font-size: 0.7em;
						}
					}

					div.tubes {
						display: none;
					}
				}
			}
		}

		div.lower {
			display: none;
		}
	}
}