@import "bootstrapmodal.scss";

section#section3 {
	width: 100vw;
	height: 500px;

	div.text {
		div.inner {
			h1 {
				color: #ed165a;
				font-family: isra;
				margin: 0;
				font-size: 3em;
				font-weight: normal;
				text-align: center;
				height: 17.5%;
			}

			p {
				color: #ed165a;
				text-align: center;
				font-family: jf;
				height: 8.5%;
			}

			div.logo {
				display: none;
			}
		}
	}

	div#videoswiper {
		width: 870px;
		height: 74%;
		margin: 0 auto;
		direction: ltr;

		div.youtubevideos {
			height: 100%;
			text-align: center;
			width: 100%;

			&.swiper-container {
				padding-top: 50px;
				padding-bottom: 50px;
				perspective: 1000px;

				div.swiper-slide {
					background-position: center;
					background-size: cover;
					width: 300px;
					height: 300px;

					div.youtubevideo {
						display: inline-block;
						position: relative;
						overflow: hidden;

						img {
							&.youtubeTv {
								height: 100%;
								position: relative;
								z-index: 2;
							}
						}

						div.youtubeThumb {
							overflow: hidden;
							position: absolute;
							top: 0;
							left: 0;
							z-index: 1;
							top: 9%;
							left: 4%;
							width: 92%;
							height: 53.1%;

							img {
								width: 100%;

								&.byheight {
									height: 100%;
								}
							}
						}
					}
				}

				div.swiper-pagination {
					bottom: 30px;

					span.swiper-pagination-bullet {
						width: 10px;
						height: 10px;
						border: 2px solid #727071;
						background: none;

						&.swiper-pagination-bullet-active {
							background: #727071;
						}
					}
				}
			}
		}
	}

	div.modals {
		div.modal {
			padding: 0 !important;

			div.modal-dialog {
				height: 92%;
				display: flex;
				align-items: center;

				div.modal-content {
					height: 338px;
					background-color: #f1ebe2;
					border-radius: 0px;

					div.title {
						position: absolute;
						width: 100%;
						top: -45px;

						p {
							font-size: 1.1em;
							color: white;
							height: auto;
							font-weight: bold;
						}
					}

					img.close {
						position: absolute;
						top: -25px;
						right: -35px;
						width: 70px;
						cursor: pointer;
						z-index: 2;
					}

					display: flex;
					align-items: center;
					width: 100%;

					video {
						width: 100%;
					}
				}
			}
		}
	}
}

/*=============================================
=            Tablets media query              =
=============================================*/

@media only screen and (max-width: $tablet_media_query) {
	section#section3 {
		height: 430px;

		div.elementscontainer {
			h1 {
				font-size: 2.5em;
			}

			p {
				font-size: 0.8em;
			}

			div#videoswiper {
				height: 335px;
				width: 640px;

				div.youtubevideos {
					padding-bottom: 0px;
					padding-top: 30px;

					div.swiper-slide {
						width: 280px;
						height: 280px;
					}
				}
			}
		}
	}
}

/*=============================================
=            Mobile media query till 320px    =
=============================================*/

@media only screen and (max-width: $mobile_media_query) {
	section#section3 {
		height: auto;

		div.elementscontainer {
			div.text {
				border-top-left-radius: 10px;
				border-top-right-radius: 10px;
				padding: 5px;
				background-color: #ed165a;

				div.inner {
					border-top-left-radius: 10px;
					border-top-right-radius: 10px;
					border: 2px dashed white;
					padding: 15px 0;

					div.logo {
						display: block;
						width: 65%;
						margin: 0 auto;

						img {
							width: 100%;
						}
					}

					h1, p {
						color: white;
					}
				}
			}

			div#videoswiper {
				width: 280px;
			}
		}

		div.modals {
			div.modal {
				div.modal-dialog {
					div.modal-content {
						width: 90%;
						position: absolute;
						height: auto;
						left: 0;
						right: 0;
						margin: auto;

						div.title {
							top: -14%;

							p {
								font-size: 3.5vw;
							}
						}
					}
				}
			}
		}
	}
}